.main-nav__logo-wrapper {
  display: flex;
  margin: 0 auto;
  width: 280px;
}

.main-nav__logo {
  .reset();
  padding-top: 6px;
  padding-bottom: 19px;
  margin-left: -70px;
  width: 147px;

  display: block;
  box-sizing: border-box;
  text-align: left;
  border-bottom: 1px solid #000000;
}

.main-nav__logo-container {
  margin: 0 auto;
  width: 320px;
}

.logo-glasses--nav {
  display: none;
}

.logo-stars--nav {
  display: none;
}

.main-nav__logotype--nav {
  fill: @white;

  &:hover {
    fill: #d22856;
  }

  &:active {
    fill: @white;
    opacity: 0.3;
  }
}

.main-nav__toggle {
  display: block;
  position: relative;
  margin-left: auto;
  margin-top: 19px;
  padding: 0;
  width: 50px;
  height: 24px;

  background-color: transparent;
  cursor: pointer;
  border: none;
}

.main-nav--nojs .main-nav__toggle {
  display: none;
}

.main-nav--closed .main-nav__toggle::before {
  content: "";

  position: absolute;
  top: 5px;
  right: 0;

  width: 50px;
  height: 3px;

  background-color: #ffffff;
  box-shadow: 0 10px 0 0 #ffffff, 0 20px 0 0 #ffffff;
}

.main-nav--closed .main-nav__toggle:hover::before {
  background-color: rgb(210, 40, 86);
  box-shadow: 0 10px 0 0 rgb(210, 40, 86), 0 20px 0 0 rgb(210, 40, 86);
}

.main-nav--closed .main-nav__toggle:active::before {
  opacity: 0.3;
}

.main-nav--opened .main-nav__icon-close {
  display: block;
  margin-left: 25px;
  fill: @white;

  &:hover {
    fill: rgb(210, 40, 86);
  }

  &:active {
    opacity: 0.3;
  }
}

.main-nav--closed .main-nav__icon-close {
  display: none;
}

.main-nav__item {
  padding-top: 16px;
  padding-bottom: 14px;
  box-sizing: border-box;
  border-bottom: 1px solid #000000;
  border-top: 1px solid #485461;
}

.main-nav__link {
  font-size: 18px;
  color: @white;
  font-weight: bold;
  line-height: 32px;
  text-transform: uppercase;

  &:hover {
    color: rgb(210, 40, 86);
  }

  &:active {
    opacity: 0.3;
  }
}

@media (min-width: @tablet-width) {
  .main-nav__logo {
    margin-left: 0;
    padding-top: 28px;
    padding-bottom: 37px;
  }

  .main-nav__logotype {
    &--glasses {
      display: inline-block;
    }
  }

  .main-nav__logo-wrapper {
    width: 620px;
  }

  .main-nav__logo-container {
    width: 620px;
  }

  .logo-glasses--nav {
    display: inline-block;
  }

  .main-nav__toggle {
    margin-top: 43px;
    margin-right: -1px;
  }

  .main-nav__item {
    padding-top: 16px;
  }
}

@media (min-width: @desktop-width) {
  .main-nav__toggle {
    display: none;
  }

  .main-nav {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.5);
  }

  .main-nav__header-wrapper {
    margin: 0 auto;
    width: 940px;
    display: flex;
  }

  .main-nav__logo-container {
    width: 314px;
  }

  .main-nav__logo-wrapper {
    .reset();
    width: auto;
  }

  .main-nav__logo {
    .reset();
    margin-right: 172px;
    padding-top: 27px;
    padding-bottom: 77px;
    width: 147px;
    height: 40px;

    border: none;
  }

  .main-nav__list {
    width: 620px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  .main-nav__item {
    .reset();
    position: relative;

    padding-top: 46px;
    padding-bottom: 44px;
    border: none;
  }

  .logo-stars--nav {
    display: inline-block;
  }

  .main-nav__link {
    font-weight: normal;
    line-height: 18px;
    text-transform: none;
  }

  .main-nav__link:active {
    color: rgba(255, 255, 255, 0.3);
  }

  .site-item--active::before {
    position: absolute;
    bottom: 33px;
    left: 0;
    content: "";
    width: 100%;
    border-top: 3px solid @white;
  }
}

@media (max-width: @mobile-width-only) {
  .main-nav--closed .main-nav__item {
    display: none;
  }

  .main-nav--closed .main-nav__logo {
    border: none;
  }
}

@media (max-width: @mobile-width-only) {
  .main-nav--closed .main-nav__item {
    display: none;
  }

  .main-nav--closed .main-nav__logo {
    border: none;
  }
}
