.page-header {
  margin: 0 auto;
  background-color: #283645;
}
.page-header--nav-closed {
  position: absolute;
  left: 0;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.3);
}
.main-nav__logo-wrapper {
  display: flex;
  margin: 0 auto;
  width: 280px;
}
.main-nav__logo {
  margin: 0;
  padding: 0;
  padding-top: 6px;
  padding-bottom: 19px;
  margin-left: -70px;
  width: 147px;
  display: block;
  box-sizing: border-box;
  text-align: left;
  border-bottom: 1px solid #000000;
}
.main-nav__logo-container {
  margin: 0 auto;
  width: 320px;
}
.logo-glasses--nav {
  display: none;
}
.logo-stars--nav {
  display: none;
}
.main-nav__logotype--nav {
  fill: #ffffff;
}
.main-nav__logotype--nav:hover {
  fill: #d22856;
}
.main-nav__logotype--nav:active {
  fill: #ffffff;
  opacity: 0.3;
}
.main-nav__toggle {
  display: block;
  position: relative;
  margin-left: auto;
  margin-top: 19px;
  padding: 0;
  width: 50px;
  height: 24px;
  background-color: transparent;
  cursor: pointer;
  border: none;
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 5px;
  right: 0;
  width: 50px;
  height: 3px;
  background-color: #ffffff;
  box-shadow: 0 10px 0 0 #ffffff, 0 20px 0 0 #ffffff;
}
.main-nav--closed .main-nav__toggle:hover::before {
  background-color: #d22856;
  box-shadow: 0 10px 0 0 #d22856, 0 20px 0 0 #d22856;
}
.main-nav--closed .main-nav__toggle:active::before {
  opacity: 0.3;
}
.main-nav--opened .main-nav__icon-close {
  display: block;
  margin-left: 25px;
  fill: #ffffff;
}
.main-nav--opened .main-nav__icon-close:hover {
  fill: #d22856;
}
.main-nav--opened .main-nav__icon-close:active {
  opacity: 0.3;
}
.main-nav--closed .main-nav__icon-close {
  display: none;
}
.main-nav__item {
  padding-top: 16px;
  padding-bottom: 14px;
  box-sizing: border-box;
  border-bottom: 1px solid #000000;
  border-top: 1px solid #485461;
}
.main-nav__link {
  font-size: 18px;
  color: #ffffff;
  font-weight: bold;
  line-height: 32px;
  text-transform: uppercase;
}
.main-nav__link:hover {
  color: #d22856;
}
.main-nav__link:active {
  opacity: 0.3;
}
@media (min-width: 660px) {
  .main-nav__logo {
    margin-left: 0;
    padding-top: 28px;
    padding-bottom: 37px;
  }
  .main-nav__logotype--glasses {
    display: inline-block;
  }
  .main-nav__logo-wrapper {
    width: 620px;
  }
  .main-nav__logo-container {
    width: 620px;
  }
  .logo-glasses--nav {
    display: inline-block;
  }
  .main-nav__toggle {
