.pop-up {
  position: fixed;
  width: 100%;
  top: 5%;
  padding-top: 46px;
  padding-bottom: 50px;
  text-align: center;
  background-color: #ffffff;
  box-shadow: 0 25px 15px -25px rgba(0, 0, 0, 0.2);
}
.pop-up--close {
  display: none;
}
.pop-up__wrapper {
  width: 280px;
  margin: 0 auto;
}
.pop-up__title {
  margin: 0;
  padding: 0;
  margin-bottom: 25px;
  font-size: 18px;
  font-weight: bold;
  line-height: 24px;
}
.pop-up__text {
  margin-bottom: 43px;
  font-size: 14px;
  line-height: 24px;
}
.pop-up__btn {
  font-size: 18px;
  font-weight: bold;
  line-height: 30px;
  box-sizing: border-box;
  color: #ffffff;
  border: solid 2px #2aab6d;
  background-color: #2aab6d;
  text-transform: uppercase;
}
.pop-up__btn:hover {
  background-color: #1c9b5e;
}
.pop-up__btn:active {
  color: rgba(255, 255, 255, 0.3);
}
.pop-up__btn:disabled {
  border: solid 2px #e5e5e5;
  color: #ffffff;
}
.pop-up__btn--failure {
  padding: 15px 125px;
}
.pop-up__btn--success {
  padding: 15px 66px;
}
@media (min-width: 660px) {
