    padding-top: 36px;
    padding-bottom: 0;
    right: 0;
    left: 0;
    margin-right: auto;
    margin-left: auto;
    box-sizing: border-box;
    width: 460px;
    box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.1), -20px 0 30px -15px rgba(0, 0, 0, 0.1), 20px 0 30px -15px rgba(0, 0, 0, 0.1), 0 20px 30px 0 rgba(0, 0, 0, 0.1);
